/* modal.css
   This file contains css definitions necessary to support modal dialog boxes using a pure HTML/JavaScript/CSS 
   approach. The definitions are taken directly from https://www.w3schools.com/howto/howto_css_modals.asp */

/* The Modal (background) */
.modal {
  display: block;
  position: fixed; /* Stay in place */
  z-index: 101; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  background: #fff;
  margin: 7% auto; /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #888;
  width: 100%; /* Could be more or less, depending on screen size */
}

.modal-title {
  text-align: center;
  width: 100%;
  font-weight: bold;
}

.modal-header {
  padding: 2px 16px;
  background-color: slategray;
  color: white;
}

.modal-body {
  padding: 2px 16px;
  color: slategray;
}

.modal-footer {
  padding: 2px 16px;
  background-color: white;
  color: slategray;
  display: flex;
  justify-content: center;
}

.close-modal-button {
  border: 2px solid slategray;
  background-color: slategray;
  color: white;
  float: right;
}

.close-modal-button:hover,
.close-modal-button:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.aboutBody {
  float: left;
  margin-right: 20px;
}
